@import "variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .content {
      .container {
        .content__actualProduct,
        .content__saleProduct {
          &__slider{
            img {
              height: 330px;
            }
            .slider {
              height: 330px;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 330px;
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts,
        .social{
          width: 75%;
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    font-size: 14px;
    line-height: 16px;
    .contentAddres {
      .contentAddres__contactForm {
        .contacts,
        .social{
          width: 80%;
        }
      }
    }
  }
}

@media screen and (max-width: 1190px), screen and (max-device-width: 1190px){
  body{
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          margin: 25px 0 0 25px;
          input[type*="text"] {
            width: 144px;
          }
          textarea {
            width: 313px;
          }
        }
        .contacts,
        .social{
          margin: 0 0 0 25px;
        }
      }
    }
  }
}

@media screen and (max-width: 1140px), screen and (max-device-width: 1140px){
  body{
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .content{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline{
            div{
              margin: 20px 40px 0 0;
              flex-direction: column;
              justify-content: flex-end;
              p{
                margin: 5px 0;
                font-size: 1em;
                line-height: 1.125em;
              }
            }
          }
          &__slider{
            img{
              height: 270px;
            }
            .slider{
              height: 270px;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 270px;
                  }
                  li {
                    a{
                      img{
                        width: 150px;
                        height: 150px;
                      }
                      p{
                        font-size: 1.25em;
                        line-height: 1.375em;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder{
          div{
            height: 150px;
            .icons{
              .arrow{
                margin: 15px 0 0 15px;
              }
              .cart,
              .box,
              .truck{
                margin-top: 20px;
                width: 70px;
                height: 70px;
              }
            }
            .learnMore{
              a{
                width: 125px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 940px), screen and (max-device-width: 940px){
  body{
    .content{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__showMore{
            a{
              width: 150px;
            }
          }
        }
        .content__howToMakeOrder{
          div{
            height: 150px;
            .icons{
              .cart,
              .box,
              .truck{
                width: 50px;
                height: 50px;
              }
            }
            .learnMore{
              a{
                width: 100px;
                height: 35px;
                font-size: 1em;
                line-height: 35px;
              }
            }
          }
        }
      }
    }
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 120px;
            height: 30px;
          }
          textarea {
            width: 266px;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 870px), screen and (max-device-width: 870px){
  body{
    .content{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__slider{
            img[alt*="img"]{
              display: none;
            }
            .slider{
              padding: 0;
              width: 100%;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 200px;
                  }
                }
              }
            }
          }
        }
      }
    }
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          display: flex;
          flex-direction: column;
          input[type*="text"], textarea {
            width: 100%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .content{
      .container{
        .content__howToMakeOrder{
          margin: 25px 0;
          flex-direction: column;
          div{
            justify-content: space-around;
            align-items: center;
            margin: 5px 0;
            width: 100%;
            height: 90px;
            .icons{
              order: 1;
              .arrow{
                margin: 0;
              }
              .cart,
              .box,
              .truck{
                margin: 0 0 0 15px;
              }
            }
            .learnMore{
              order: 3;
              a{
                width: 165px;
              }
            }
            .info{
              order: 2;
              margin-left: 0;
            }
            .icons,
            .learnMore,
            .info{
              width: unset;
            }
          }
        }
      }
    }
    .contentAddres{
      flex-direction: column;
      iframe{
        width: 100%;
      }
      &__contactForm{
        width: 100%;
        .comentary{
          margin: 25px 8% 0!important;
          align-items: center;
          width: 90%;
          button{
            margin: 0;
            width: 80%;
          }
        }
        .contacts{
          margin: 25px 8% 0!important;
          align-items: center;
          width: 90%;
        }
        .social{
          margin: 25px 8%!important;
          align-items: center;
          width: 90%;
        }
      }
    }
  }
}